.swiper-container {
    /* margin-top: 20px; */
    padding: 0.6rem 1rem;
    display: flex;
    align-items: center;
    justify-content: space-between;
    font-size: 15px;
  }
  .rollScreen_container {
    width: 100%;
    line-height: 25px;
    text-align: center;
    display: inline-block;
    position: relative;
    overflow: hidden;
  }
  .item {
    width: 100%;
    overflow: hidden;
    white-space: nowrap;
    text-overflow: ellipsis;
    transition: all 0.3s;
  }
  .rollScreen_list:hover .item {
    color: #8e8cd8;
  }
  .rollScreen_list {
    transition: 1s linear;
  }
  .rollScreen_list_unanim {
    transition: none;
  }
  .arrow {
    animation: 1s passing infinite;
  }
  @keyframes passing {
    0% {
      transform: translateX(-50%);
      opacity: 0;
    }
    50% {
      transform: translateX(0);
      opacity: 1;
    }
    100% {
      transform: translateX(50%);
      opacity: 0;
    }
  }